<template>
  <div>
    <el-dialog
      title="查看出库单"
      :visible.sync="isVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :lock-scroll="true"
      width="95%"
      @close="cancel">
        <el-button type="primary" class="printBtn" @click="print">打印</el-button>
      <el-form ref="orderInfo" id="printPage" :model="orderInfo"  label-width="80px">
        <div class="print-form">
        <el-row>
          <el-col :span="5">
            <el-form-item label="商户:" prop="merchants">
              <span>{{getMerchantName(orderInfo.goodsDeliverMerchantUuid)}}</span>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="订单类型:" prop="goodsDeliverType">
              <span>{{deliverTypeFormatter(orderInfo.goodsDeliverType)}}</span>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="送货日期:" prop="goodsDeliverDate">
              <span>{{formatTime(orderInfo.goodsDeliverDate)}}</span>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="5">
            <el-form-item label="客户姓名:" prop="customerName">
              <span>{{orderInfo.customerName}}</span>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="客户电话:" prop="customerTelephone">
              <span>{{orderInfo.customerTelephone}}</span>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="客户地址:" prop="customerAddress">
                <span>{{orderInfo.customerAddress}}</span>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="备注:" prop="goodsDeliverRemark">
              <span>{{orderInfo.goodsDeliverRemark}}</span>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="5">
            <el-form-item label="电梯:" prop="customerLiftFlag">
              <span>{{orderInfo.customerLiftFlag === '0' ? '有' : orderInfo.customerLiftFlag === '1' ? '无' : '未知'}}</span>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="是否安装:" prop="customerNeedInstall">
              <span>{{orderInfo.customerNeedInstall === '0' ? '是' : orderInfo.customerNeedInstall === '1' ? '否' : '未知'}}</span>
            </el-form-item>
          </el-col>
        </el-row>
        </div>
        <el-row class="print-table">
          <el-table
            ref="orderTable"
            stripe
            :border="true"
            tooltip-effect="light"
            v-loading="isListLoading"
            :data="tableData">
            <el-table-column type="index" label="序号" width="50" fixed="left" ></el-table-column>
            <el-table-column prop="productBrand" label="品牌" ></el-table-column>
            <el-table-column prop="productName" label="货品名称"></el-table-column>
            <el-table-column prop="productModel" label="型号" :formatter="dealModelName" ></el-table-column>
            <el-table-column prop="productStandard" label="规格"></el-table-column>
            <el-table-column prop="productColor" label="颜色" :show-overflow-tooltip="true" ></el-table-column>
            <el-table-column prop="storageName" label="库位"></el-table-column>
            <el-table-column prop="productSeq" label="二维码" width="150px"></el-table-column>
            <!--<el-table-column prop="deliverItemPack" label="包装形式" :formatter="PackFormatter" min-width="110"></el-table-column>-->
            <el-table-column prop="productType" label="货品类型" :formatter="typeFormatter"></el-table-column>
            <!--<el-table-column prop="productItemUuid" label="子件" :formatter="itemFormatter" min-width="140" :show-overflow-tooltip="true" ></el-table-column>-->
            <el-table-column prop="deliverItemCount" label="数量"  ></el-table-column>
            <el-table-column label="备注" min-width="120" prop="goodsDeliverRemark" :show-tooltip-when-overflow="true" ></el-table-column>
            <!--<el-table-column prop="goodsDeliverRemark" label="备注" min-width="100" :show-overflow-tooltip="true" ></el-table-column>-->
            <!--<el-table-column prop="productMain" label="计件类型" min-width="100" :show-overflow-tooltip="true"></el-table-column>-->
          </el-table>
        </el-row>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
  import moment from 'moment'
  import Validator from '@/utils/extendValidate/extendValidate'
  import GMap from '../../Warehouse/Components/Map'
  import CommonTable from '@/components/commonTable'
  import TemplateCreate from '@/views/Development/ProductTemplate/Components/TemplateCreate'
  import TemplateImport from '@/views/Development/ProductTemplate/Components/ImportTemplate'
  import { uploadTemplate } from '@/api/warehouse'
  import { getDeliveryTemplateList, getEntryOrderDetail, updateDeliveryOrder, deleteEntryOrder,getPickList } from '@/api/order'
  import { getAllMerchantsList } from '@/api/common'
  import { setSession, getSession } from '@/utils/session'

  export default {
    name: 'OrderCreate',
    data () {
      return {
        isVisible: false,
        orderInfo: {
          goodsDeliverMerchantName: '',
          goodsDeliverMerchantUuid:'',
          goodsDeliverType: '',
          goodsDeliverDate: '',
          customerName: '',
          customerAddress: '',
          customerTelephone: '',
          warehouseName: '',
          warehouseUuid: '',
          goodsDeliverRemark: '',
          customerServicePhone: '',
          customerLiftFlag: '',
          customerNeedInstall: ''
        },
        isListLoading:false,
      goodsDeliverType: [{
          value: 1,
          label: "仓库出货"
        }, {
          value: 2,
          label: "自提"
        }],
        deliverItemPack: [{
          value: '1',
          label: "有包装"
        }, {
          value: '2',
          label: "无包装"
        }, {
          value: '3',
          label: "其他"
        }],
        existModel: [],
        tableData: [],
        addressInfo: {}
      }
    },
    props: {
      show: {
        type: Boolean,
        default: false
      },
      warehouseList: {
        type: Array
      },
      merchantsList: {
        type: Array
      },
      selectedOrderInfo: {
        type: Object
      }
    },
    watch: {
      show (isShow) {
        this.orderInfo = {}
        if(isShow){
          this.orderInfo = Object.assign({},this.selectedOrderInfo)
          this.isListLoading = true
          getPickList({deliverOrderId:this.orderInfo.goodsDeliverUuid}).then(res => {
            if (!res.error) {
              this.isListLoading = false
              this.tableData = res.pickLists
            }
          })
        }
        this.isVisible = isShow
      }
    },
    methods: {
      deliverTypeFormatter (type) {
        switch (type) {
          case '1':
            return '仓库出货'
            break
          case '2':
            return '自提'
            break
          case '3':
            return '顾客端配送安装'
            break
          case '4':
            return '展厅上样'
            break
          case '5':
            return '展厅提货送样品'
            break
          case '6':
            return '物流提货送货'
            break
          case '7':
            return '展厅调场'
            break
          case '8':
            return '外展布场'
            break
          case '9':
            return '外地送货'
            break
          case '10':
            return '售后维修'
            break
          case '11':
            return '搬家(含顾客家拆装)'
            break
          default:
            return ''
        }
      },
      dealModelName (row) {
        if (row.remark) {
          return row.productModel+'「'+row.remark+'」'
        } else {
          return row.productModel
        }
      },
      getMerchantName (id) {
        if(id){
          return this.merchantsList.find(item => item.userUuid === id).userName
        }
      },
      PackFormatter (row) {
        return row.deliverItemPack === '1' ? '有包装' : row.deliverItemPack === '2' ? '无包装' : '其他'
      },
      typeFormatter (row) {
        return row.productType === '0' ? '小件' : row.productType === '1' ? '套件' : row.productType === '2' ? '单品' : '未知'
      },
      itemFormatter (row) {
        if (row.productType == '1' && row.productItems.length) {
          let productItem = row.productItems.find( item => {
            return row.productTempleteItemId === item.productItemUuid
          })
          if(productItem) {
            return productItem.productItemName
          } else {
            return ''
          }
        }
      },
      formatTime (date) {
        if (date) {
          return moment(date).utcOffset(8).format('YYYY-MM-DD')
        }
      },
      cancel () {
        this.existModel = []
        this.tableData =[]
        this.$refs.orderInfo.resetFields()
        this.$emit('update:show', false)
        this.isVisible = false
      },
      print () {
        let warehouseUuid = this.orderInfo.warehouseUuid ? this.orderInfo.warehouseUuid : getSession('warehouseUuid') ;
        console.log("list",this.warehouseList)
        let warehouseName = this.warehouseList.filter((item)=>{
          return item.warehouseUuid == warehouseUuid
        })[0].warehouseName ;
        let headhtml = "<!DOCTYPE html><html style='float:none;'><head><title></title><style>.el-table__fixed{display: none;}table{width:100% !important;margin: 0 50px 0 50px;}h2{text-align:center}.print-form{width:100%;margin:50px;position:relative;box-sizing:border-box;}th{text-align:left}.print-form .el-row{box-sizing: border-box;width:50%;float:left;}  .print-form .el-row:nth-child(3){box-sizing: border-box;width:100%;float:left;} .el-row .el-col{padding:10px 0px ;} .el-form-item{display:flex} label{font-weight:bold}.el-table__header-wrapper table{padding-top:50px;}</style></head><body id='printWindow' style='float:none;line-height: 24px;font-size: 14px;'>";
        let foothtml = "</body></html>";
        let titlehtml = `<h2>星和宅配(${warehouseName})送货单</h2>`
        let printHtml = document.getElementById('printPage').innerHTML;
        let result =headhtml + titlehtml + printHtml + foothtml
        let printer = window.open("",'','');
        printer.document.write(result);
        printer.print();
      }
    },
    components: {
      CommonTable,
      TemplateCreate,
      TemplateImport,
      GMap
    }
  }
</script>

<style lang="scss" scoped>
  .printBtn{
    position: absolute;
    top: 15px;
    left: 150px;
  }
</style>



